* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

img {
  max-width: 100%;
}

body {
  font-family: "League Spartan", sans-serif;
  color: #fff;
  font-style: normal;
  line-height: normal;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Antonio", sans-serif;
  text-transform: uppercase;
}

h1 {
  font-size: 8rem;
  font-weight: 400;
  line-height: normal;
}

h2 {
  font-size: 4rem;
  font-weight: 400;
  letter-spacing: -1.5px;
}

h3 {
  font-size: 1.2rem;
  font-weight: 700;
  line-height: 2.5rem;
  letter-spacing: 2.571px;
}

h4 {
  font-size: 1.1rem;
  font-weight: 700;
  line-height: 2.5rem;
  letter-spacing: 1px;
}

p {
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 2.5rem;
}

li {
  font-size: 1.1rem;
  font-weight: 700;
  line-height: 2.5rem;
  letter-spacing: 1px;
  text-transform: uppercase;
}

button {
  color: #fff;
  font-family: "Antonio", sans-serif;
  font-size: 4rem;
  font-weight: 400;
  letter-spacing: -1.5px;
  text-transform: uppercase;
}

button:active {
  background-color: #419ebb;
}

main {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.planet {
  background-color: #070724;
  background-size: cover;
}

.main-heading {
  font-family: "Antonio", sans-serif;
  font-size: 2.8rem;
  font-weight: 400;
  letter-spacing: -0.1rem;
}

.navigation {
  width: 90%;
  max-width: 144rem;

  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;

  padding: 0 0 0 3.2rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);

  margin-bottom: 12.6rem;
}

.button-hamburger {
  display: none;
}

.navigation h1 {
  padding: 2.2rem 0 3.2rem 0;
}

.list-of-planets {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3.3rem;
}

.list-of-planets li {
  color: #fff;
  font-family: "League Spartan", sans-serif;
  font-size: 1.1rem;
  font-style: normal;
  font-weight: 700;
  line-height: 2.5rem;
  letter-spacing: 1px;
  text-transform: uppercase;
  border-top: 0.4rem solid transparent;
  padding: 3.2rem 0;
  opacity: 0.75;
}

.list-of-planets li:hover {
  cursor: pointer;
  color: #fff;
  opacity: 1;
}

.nav-li-active .on-click {
  border-top: 0.4rem solid #fff;
}

.planet-details {
  width: 90%;
  max-width: 144rem;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.secondary-img {
  max-width: 16.3rem;
  position: absolute;
  top: 59%;
}

.planet-desc {
  width: 35.8%;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.planet-img-box {
  width: 64.2%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.planet-desc-box {
  max-width: 35rem;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.planet-name {
  font-family: "Antonio", sans-serif;
  font-size: 8rem;
  margin-bottom: 2.3rem;
}

.planet-text {
  margin-bottom: 2.4rem;
}

.planet-desc-box p {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 2.5rem;
}

.wiki-link {
  margin-bottom: 3.9rem;
}

.planet-desc-box a {
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  line-height: 2.5rem;
  text-decoration-line: underline;
  opacity: 0.5;
}

.planet-tabs {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 1.6rem;
}

.planet-tab {
  width: 100%;
  max-width: 35rem;
  background-color: transparent;

  color: #fff;
  font-size: 1.2rem;
  font-weight: 700;
  line-height: 2.5rem;
  letter-spacing: 2.571px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 1.1rem 0 1.2rem 2.8rem;
  font-family: "League Spartan", sans-serif;

  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 2.8rem;
}

.planet-tab:hover {
  background-color: rgba(255, 255, 255, 0.2);
  cursor: pointer;
  border: 1px solid transparent;
}

/* active */
.planet-tab-active {
  background-color: #419ebb;
  border: 1px solid transparent;
}

.planet-tab-title {
  font-size: 1.2rem;
  font-weight: 700;
  line-height: 2.5rem;
  letter-spacing: 2.571px;
  opacity: 0.5;
}

.planet-in-numbers {
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 3rem;
  margin-top: 8.7rem;
  margin-bottom: 5.6rem;
}

.planet-fact {
  max-width: 25.5rem;
  flex-grow: 1;
  padding: 2rem 0 2.7rem 2.3rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.planet-fact h4 {
  font-size: 1.1rem;
  font-weight: 700;
  line-height: 2.5rem;
  letter-spacing: 1px;
  opacity: 0.5;
  margin-bottom: 0.4rem;
}

.planet-fact p {
  color: #fff;
  font-family: "Antonio", sans-serif;
  font-size: 4rem;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -1.5px;
  text-transform: uppercase;
}

.show-for-desktop-tablet {
  display: block;
}

/* Tablets */

@media (min-width: 426px) and (max-width: 768px) {
  .main-heading {
    letter-spacing: -1.05px;
  }

  .navigation {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    align-items: center;

    padding: 3.2rem 0 0 0;
    margin-bottom: 14.6rem;
  }

  .button-hamburger {
    display: none;
  }

  .navigation h1 {
    padding: 0;
  }

  .list-of-planets {
    flex-wrap: wrap;
    row-gap: 0;
  }

  .list-of-planets li {
    border-top: 0.4rem solid transparent;
    border-bottom: 0.4rem solid transparent;
    margin-top: 2rem;
    padding: 1rem 0;
  }

  .list-of-planets li:hover {
    border-top: 0.4rem solid transparent;
    cursor: pointer;
    color: #fff;
    opacity: 1;
  }

  .nav-li-active.on-click {
    border-top: 0.4rem solid transparent;
    border-bottom: 0.4rem solid transparent;
  }

  .planet-details {
    width: 90%;
    max-width: 144rem;

    flex-direction: column;
  }

  .planet-desc {
    width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }

  .planet-img-box {
    width: 100%;
    margin-bottom: 13rem;
  }

  .planet-desc-box {
    max-width: 35rem;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
  }

  .secondary-img {
    max-width: 16.3rem;
    position: absolute;
    top: 79%;
  }

  .planet-name {
    font-family: "Antonio", sans-serif;
    font-size: 4.8rem;
    margin-bottom: 2.4rem;
  }

  .planet-text {
    color: #fff;
    font-family: "League Spartan", sans-serif;
    font-size: 1.1rem;
    font-style: normal;
    font-weight: 400;
    line-height: 2.2rem;
    margin-bottom: 3.2rem;
  }

  .planet-desc-box p {
    font-size: 1.2rem;
  }

  .wiki-link {
    margin-bottom: 2.7rem;
  }

  .planet-desc-box a {
    font-size: 1.2rem;
  }

  .planet-tabs {
    align-items: flex-end;
  }

  .planet-tab {
    max-width: 28.1rem;
    font-size: 0.9rem;
    padding: 0.8rem 0 0.7rem 2rem;
    gap: 1.7rem;
  }

  .planet-tab {
    background-color: transparent;
    /* border-bottom: 4px solid transparent; */
  }

  .planet-tab-title {
    font-size: 0.9rem;
  }

  .planet-in-numbers {
    gap: 1.1rem;
    margin-top: 2.7rem;
    margin-bottom: 3.6rem;
  }

  .planet-fact {
    width: 16.4rem;
    padding: 1.6rem 0 1.9rem 1.5rem;
  }

  .planet-fact h4 {
    font-size: 0.8rem;
    margin-bottom: 0.6rem;
  }

  .planet-fact p {
    font-size: 2.4rem;
    letter-spacing: -0.9px;
  }

  .show-for-desktop-tablet {
    display: block;
  }
}

/* Mobile */
@media (min-width: 320px) and (max-width: 426px) {
  .main-heading {
    letter-spacing: -1.05px;
  }

  .navigation {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    padding: 0;
    margin-bottom: 14.6rem;
    border-bottom: none;
  }

  .heading-box {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .button-hamburger {
    background-color: transparent;
    border: none;
    align-self: center;

    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 2.4rem 0 0;
  }

  .mobile-menu {
    width: 100%;
    height: 65vh;
    display: block;
    background-color: #070724;
    z-index: 100;
    padding: 0 2.4rem 0 3.2rem;
  }

  /* .mobile-menu-false {
    display: none;
  } */

  .planet-circle-box {
    display: flex;
    align-items: center;
    gap: 2.5rem;
  }

  .planet-circle {
    width: 2rem;
    height: 2rem;
    border: none;
    border-radius: 100%;
    background-color: red;
  }

  .navigation h1 {
    padding: 1.6rem 0 1.7rem 2.4rem;
  }

  /* 1 */
  .list-of-planets {
    flex-wrap: wrap;
    row-gap: 0;
    /* display: none; */
  }

  .list-of-planets li {
    border-top: 0.4rem solid transparent;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding: 2rem 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .nav-li-active.on-click {
    /* border-top: 0.4rem solid transparent; */
    /* border-bottom: 0.4rem solid #419ebb; */
  }

  .planet-details {
    flex-direction: column;
  }

  .planet-desc {
    width: 100%;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
  }

  .planet-img-box {
    width: 50%;
    margin-bottom: 9rem;
  }

  .planet-desc-box {
    max-width: 35rem;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  .secondary-img {
    max-width: 10.3rem;
    position: absolute;
    top: 50%;
  }

  .planet-name {
    font-family: "Antonio", sans-serif;
    font-size: 4rem;
    margin-bottom: 1.6rem;
  }

  .planet-text {
    color: #fff;
    font-family: "League Spartan", sans-serif;
    font-size: 1.1rem;
    font-style: normal;
    font-weight: 400;
    line-height: 2.2rem;
    margin-bottom: 3.2rem;
  }

  .planet-desc-box p {
    font-size: 1.2rem;
  }

  .wiki-link {
    margin-bottom: 2.8rem;
  }

  .planet-desc-box a {
    font-size: 1.2rem;
  }

  .planet-tabs {
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 4.3rem;

    position: absolute;
    top: 7rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  }

  .planet-tab {
    max-width: 8rem;
    font-size: 0.9rem;
    padding: 2rem 0 1.7rem 0;
    border: 1px solid transparent;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .planet-tab {
    background-color: transparent !important;
  }

  /* active */
  .planet-tab-active {
    background-color: none;
    border-bottom: 1px solid rgba(255, 255, 255) !important;
  }

  .planet-tab-title {
    font-size: 0.9rem;
    display: none;
  }

  .mobile-view {
    display: none;
  }

  .planet-in-numbers {
    gap: 1.1rem;
    margin-top: 2.7rem;
    margin-bottom: 3.6rem;
  }

  .planet-fact {
    width: 16.4rem;
    padding: 1.6rem 2.4rem 1.9rem 2.4rem;
    width: 100%;
    max-width: 32.7rem;

    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .planet-fact h4 {
    font-size: 0.8rem;
    margin-bottom: 0;
  }

  .planet-fact p {
    font-size: 2rem;
    letter-spacing: -0.75px;
  }

  .show-for-desktop-tablet {
    display: none;
  }
}
